* {
  margin: 0;
  padding: 0;
  font-family: "PingFangSC";
}
ul,
li,
ol {
  list-style: none;
}
button,
input {
  outline: none;
}
a,
a:hover,
a:focus,
a:visited {
  text-decoration: none;
  outline: none;
}

.layout-home {
  width: 100%;
  height: 100vh;
  position: relative;
}
.layout-home .banner div {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  position: absolute;
  z-index: 1;
}
.layout-home .banner div.active {
  z-index: 10;
}
.layout-home .banner div img {
  width: 100%;
  height: 100%;
}
.layout-home .banner-dot {
  position: absolute;
  width: 100%;
  height: 40px;
  z-index: 100;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.layout-home .banner-dot span {
  cursor: pointer;
  border-radius: 50%;
  background: #fff;
  opacity: 0.3;
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 15px;
}
.layout-home .banner-dot span.active {
  opacity: 1;
}
.layout-home .banner-btn {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}
.layout-home .banner-btn .btn-left,
.layout-home .banner-btn .btn-right {
  position: absolute;
  z-index: 200;
  color: #fff;
  width: 50%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.layout-home .banner-btn .btn-left {
  left: 0;
}
.layout-home .banner-btn .btn-right {
  text-align: right;
  right: 0;
}

.layout-header {
  width: 100%;
  height: 50px;
  box-sizing: border-box;
  padding: 10px;
  background: #373d41;
  font-size: 16px;
  display: flex;
  align-items: center;
  color: #fff;
  justify-content: space-between;
}
.layout-header .layuut-header-left {
  display: flex;
  align-items: center;
}
.layout-header .layuut-header-left .layout-header-menu {
  font-size: 13px;
  display: inline-block;
  margin-right: 10px;
}
.layout-header-dropdown .ant-dropdown-menu-item:hover,
.layout-header-dropdown .ant-dropdown-menu-submenu-title:hover {
  background-color: #1890ff;
  color: #fff;
}

.layout-menu {
  display: inline-block;
  background: #001529;
}

h3 {
  display: flex;
  justify-content: center;
  color: gold;
}
#root {
  width: 100%;
  height: 100%;
}
#root .layout-app {
  width: 100%;
  height: 100%;
}
#root .layout-app .layout-content {
  display: flex;
  min-height: 100vh;
  overflow: hidden;
  width: 100%;
}
#root .layout-app .layout-content .layout-contentR {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  padding: 10px 20px;
}
#root .layout-app .layout-content .ant-back-top {
  right: 50px;
  bottom: 50px;
}
#root .layout-app .layout-footer {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background: #f0f2f5;
}

